.navbar_container {
  background-color: white;
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
  padding: 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.navbar_desktop {
  margin-left: 160px;
  margin-right: 160px;
  margin-top: 25px;
  margin-bottom: 25px;
}

.desktop_items {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.desktop_left_items {
  display: flex;
  flex-direction: row;
}

.desktop_right_items {
  display: block;
}

.desktop_right_items div {
  margin: 0px;
}

.desktop_item {
  display: flex;
  font-size: 18pt;
  margin-right: 40px;
}

.desktop_item:hover {
  cursor: pointer;
  font-weight: bold;
  border-bottom: 1px solid #213343;
}

.desktop_img_item {
  margin-right: 40px;
}

.desktop_img_item:hover {
  border-bottom: none;
}

#desktop_github_logo {
  margin-left: 10px;
}

#desktop_thoth_logo {
  margin: 0px;
}

.navbar_mobile {
  margin-left: 10px;
  margin-right: 0px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.mobile_items {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mobile_items-menu_open {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
}

.mobile_left_items {
  display: flex;
  justify-content: left;
}

.mobile_right_items {
  display: flex;
  justify-content: center;
}

.mobile_menu_logo {
  position: absolute;
  left: 50%;
  top: 5px;
  transform: translateX(-50%);
}

.mobile_menu_button {
  background: none;
  border: none;
  cursor: pointer;
  margin-right: 5px;
}

.mobile_menu_items {
  display: inline;
  justify-content: center;
}

.mobile_menu_item {
  display: inline-block;
  font-weight: bold;
}

.link {
  color: #213343;
  text-decoration: none;
}

@media (max-width: 306px) {
  .mobile_menu_item {
    font-size: 10pt;
    margin-right: 8px;
  }
}

@media (min-width: 307px) and (max-width: 314px) {
  .mobile_menu_item {
    font-size: 12pt;
    margin-right: 8px;
  }
}

@media (min-width: 315px) and (max-width: 340px) {
  .mobile_menu_item {
    font-size: 12pt;
    margin-right: 10px;
  }
}

@media (min-width: 341px) and (max-width: 360px) {
  .mobile_menu_item {
    font-size: 12pt;
    margin-right: 12px;
  }
}

@media (min-width: 361px) {
  .mobile_menu_item {
    font-size: 12pt;
    margin-right: 14px;
  }
}

@media (max-width: 768px) {
  .navbar_mobile {
    display: grid;
  }
  .navbar_desktop {
    display: none;
  }
}

/* Estilos para desktops */
@media (min-width: 769px) {
  .navbar_desktop {
    display: flex;
  }
  .navbar_mobile {
    display: none;
  }
}
